import document from 'global/document';
import window from 'global/window';
import axios from '~global-js/helpers/axios';
import generatePath from '~global-js/helpers/router';
import customEvents from '~global/variables/custom-events.json';
import phoneNumberTemplate from '~components/footer/templates/phone-number.hbs';
import getEnvironment, { environments } from '~global-js/helpers/environment';
import { isSmallScreen, isMediumScreen, isLargeScreen } from '~global-js/helpers/breakpoints';
import { getLocale } from '~global-js/helpers/locale';
import { event, reloadDataAttributeElement } from '~global-js/helpers/tracking';
import appDownloadData from '~global-js/helpers/app-download';

export default class FooterComponent {
  constructor() {
    this.languageSelector = document.getElementById('language-selector');
    this.footerPhoneContainer = document.getElementById('footer-phone-number');
    this.newsletterForm = document.getElementById('footer-newsletter-form');
    this.newsletterInput = document.getElementById('form-newsletter-input');
    this.weChatIcon = document.querySelector('.footer__wechat');
    this.footerAccount = document.querySelector('.footer__nav-item--partner');
    this.trustPilotWidgets = document.querySelectorAll('.trustpilot-widget');
  }

  init() {
    this.addLanguageChangeListener();
    this.addNewsletterSubmitListener();
    document.addEventListener(
      customEvents.geoIpPhoneNumberInitialised.name,
      this.updatePhoneNumber
    );
    this.weChatQRCode();
    this.setFooterCopyRightValue();
    this.icpNumberEvent();
    if (this.footerAccount) {
      this.footerLinkUpdate();
    }
    this.appQRCodeUrl();
    if (this.trustPilotWidgets && this.trustPilotWidgets.length > 0) {
      this.showTrustPilotListener();
      document.addEventListener('scroll', this.showTrustPilotListener);
    }
  }

  addLanguageChangeListener() {
    this.languageSelector.addEventListener('click', () => {
      event({
        c: 'footer',
        a: 'click',
        l: 'locale:open',
        mp: [{
          type: 'track',
          key: 'Clicked Footer Btn',
          params: {
            'Footer Btn': 'Language',
          },
        }],
      });
    });
    this.languageSelector.addEventListener('change', FooterComponent.handleLanguageChange);
  }

  isTrustPilotInView = () => {
    let hasWidgetInView = false;
    for (let i = 0; i < this.trustPilotWidgets.length; i++) {
      const widget = this.trustPilotWidgets[i];
      const rect = widget.getBoundingClientRect();
      const elemTop = rect.top;
      const elemBottom = rect.bottom;

      const isVisible = (elemTop >= 0) && (elemBottom <= window.innerHeight);
      hasWidgetInView = isVisible || hasWidgetInView;
    }
    return hasWidgetInView;
  }


  showTrustPilot = () => {
    // code extract from https://widget.trustpilot.com/bootstrap/v5/tp.widget.sync.bootstrap.min.js
    for (let i = 0; i < this.trustPilotWidgets.length; i++) {
      const widget = this.trustPilotWidgets[i];
      if (widget && widget.firstChild && widget.firstChild.tagName !== 'IFRAME') {
        widget.removeChild(widget.firstChild);
      }
    }

    const scriptUrl = 'https://widget.trustpilot.com/bootstrap/v5/tp.widget.bootstrap.min.js';
    const script = document.createElement('script');
    script.src = scriptUrl;
    script.async = true;
    document.querySelector('head').appendChild(script);
    document.removeEventListener('scroll', this.showTrustPilotListener);
  }

  showTrustPilotListener = () => {
    if (this.isTrustPilotInView()) {
      this.showTrustPilot();
    }
  }

  addNewsletterSubmitListener() {
    this.newsletterForm.addEventListener('submit', this.handleNewsletterSubmit);
  }

  handleNewsletterSubmit = (e) => {
    e.preventDefault();
    event({
      c: 'footer',
      a: 'click',
      l: 'subscribe',
    });
    const email = this.newsletterInput.value;

    if (email === '') {
      this.newsletterForm.classList.remove('footer__newsletter--valid');
      this.newsletterForm.classList.add('footer__newsletter--error');
      return;
    }

    axios.post(generatePath('newsletter_signup'), {
      newsletter_signup_email: email,
    }).then((response) => {
      if (response.status) {
        this.newsletterForm.classList.remove('footer__newsletter--error');
        this.newsletterForm.classList.add('footer__newsletter--valid');

        event({
          c: 'footer',
          a: 'click',
          l: 'subscribe:success',
        });
      } else {
        this.newsletterForm.classList.remove('footer__newsletter--valid');
        this.newsletterForm.classList.add('footer__newsletter--error');
      }
    });
  };

  updatePhoneNumber = (e) => {
    const template = phoneNumberTemplate({
      phone: e.detail,
    });

    if (e.detail && e.detail.length > 1) {
      this.footerPhoneContainer.classList.add('footer__call-us-numbers--more');
    }

    this.footerPhoneContainer.innerHTML = template;
    reloadDataAttributeElement();
  };

  static handleLanguageChange(e) {
    const newLanguage = e.target.value;

    event({
      c: 'footer',
      a: 'click',
      l: `locale:${newLanguage}`,
      mp: [{
        type: 'track',
        key: 'Changed Language',
        params: {
          'Language Type': newLanguage,
        },
      }],
    });

    window.location.href = `/set/language/${newLanguage}`;
  }

  weChatQRCode() {
    if (isSmallScreen() || isMediumScreen()) {
      const blankArea = document.querySelector('.footer');

      if (typeof this.weChatIcon !== 'undefined') {
        this.weChatIcon.addEventListener('click', (e) => {
          this.weChatIcon.classList.toggle('footer__wechat--active');
          e.stopPropagation();
        });
      }

      blankArea.addEventListener('click', () => {
        const weChatActive = document.querySelector('.footer__wechat--active');
        if (weChatActive) {
          this.weChatIcon.classList.remove('footer__wechat--active');
        }
      });
    }
  }

  footerLinkUpdate = () => {
    this.footerAccount.getElementsByTagName('a')[0].href =
      getEnvironment() === environments.PROD
        ? `//partner.student.com/${getLocale()}`
        : `//partner.dandythrust.com/${getLocale()}`;
  };

  appQRCodeUrl = () => {
    const locale = getLocale();

    let deviceType = null;
    if (/iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream) {
      deviceType = 'IOS';
    }
    if (navigator.userAgent.toLowerCase().indexOf('android') > -1) {
      deviceType = 'Android';
    }

    const trigger = document.querySelector('.js-app-download-footer');

    if (!trigger) {
      return;
    }

    if (locale === 'zh-cn' && isLargeScreen()) {
      trigger.addEventListener('mouseenter', () => {
        event({
          mp: [{
            type: 'track',
            key: 'Clicked App-download Btn',
            params: {
              'Click Placement': 'Footer',
              'Action Type': 'Hover',
            },
          }],
        });
      });

      const footerAppQRCode = document.querySelector('.footer__download__qrcode-img');

      if (footerAppQRCode) {
        footerAppQRCode.setAttribute('src', appDownloadData().qrCode);
      }
    }


    if (!isLargeScreen()) {
      trigger.addEventListener('click', () => {
        let downloadType = 'From Mobile';
        let link = appDownloadData().link;

        if (locale !== 'zh-cn') {
          link = deviceType === 'IOS' ? appDownloadData('row').appStore : appDownloadData('row').googlePlay;
          downloadType = deviceType === 'IOS' ? 'App Store' : 'Google Play';
        }

        event({
          mp: [{
            type: 'track',
            key: 'Clicked App-download Btn',
            params: {
              'Click Placement': 'Footer',
              'Action Type': 'Click',
              'Download Type': downloadType,
            },
          }],
        });

        window.open(link);
      });
    }
  };

  icpNumberEvent = () => {
    const copyRight = document.querySelector('.footer__copyright');
    const footerLegal = document.querySelector('.footer__legal');

    const container = document.createElement('div');
    container.classList.add('footer__icp');
    copyRight.parentNode.insertBefore(container, copyRight.nextSibling);

    const link = document.createElement('a');
    const linkSummary = document.createTextNode('沪ICP备09005001号-5');
    link.appendChild(linkSummary);
    link.target = '_blank';
    link.href = 'http://www.beian.miit.gov.cn';
    link.rel = 'nofollow';
    link.classList.add('footer__icp-number');

    const policeLink = document.createElement('a');
    const policeLinkSummary = document.createTextNode('沪公网安备 31010602003495号');
    policeLink.appendChild(policeLinkSummary);
    policeLink.target = '_blank';
    policeLink.href = 'http://www.beian.gov.cn/portal/registerSystemInfo?recordcode=31010602003495';
    policeLink.rel = 'nofollow';
    policeLink.classList.add('footer__icp-number', 'footer__icp-number--police');

    if (getLocale() === 'zh-cn') {
      container.appendChild(link, container.nextSibling);
      container.appendChild(policeLink, container.nextSibling);
    } else if (window.hurricane.clientInfo && window.hurricane.clientInfo.country
      && window.hurricane.clientInfo.country.toUpperCase() === 'CN' && footerLegal) {
      footerLegal.classList.add('footer__legal--icp');
      container.appendChild(link, container.nextSibling);
      container.appendChild(policeLink, container.nextSibling);
    }
  }

  setFooterCopyRightValue = () => {
    const copyRight = document.querySelector('.footer__copyright');
    const year = new Date().getFullYear().toString();
    const prefix = '&copy;&nbsp;';

    if (getLocale() === 'zh-cn') {
      const copyRightText = '&nbsp;STUDENT.COM学旅家 江曲投资咨询（上海）有限公司';
      copyRight.innerHTML = prefix.concat(year, copyRightText);
    } else {
      const copyRightText = '&nbsp;Student.com';
      copyRight.innerHTML = prefix.concat(year, copyRightText);
    }
  }
}



